import React from 'react';
import axios from 'axios';
import './style.css';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      joke: "Welcome - Press the button below to get your ass clicked."  
    };
    
    this.getJoke = this.getJoke.bind(this);
  }

  getJoke() {
    axios.get('https://api.icndb.com/jokes/random?limitTo=[nerdy]')
    .then((response) => {
      this.setState({ joke: response.data.value.joke })
      console.log(response.data)
    })
   
    .catch((error)=>{
      console.log(error);
      this.setState({ 
       joke: "Something has gone wrong - apologies!"
      })
   });  
  }
  
  render() {
    
    return(
      
      <div className="container">

        <img className="logo" src={require('./mcrcodes.png')} alt="The Manchester Codes logo in black and white lanscape format."/>

        <h1>Chuck Norris Random Joke Generator</h1>

        <h2>Need a short break from coding? You're in the right place!</h2>

        <h3 dangerouslySetInnerHTML={{__html: this.state.joke}}></h3>

        <button 
          type="button" 
          className="joke-button"
          onClick={this.getJoke}>
          Random Joke</button>

        <p><img src={require('./chuck1.jpg')} alt="A portrait image of Chuck Norris."/></p>

        <p>Welcome to the random Chuck Norris jokes generator. This is a single-page app created using ReactJS, JSX, HTML, CSS and <a href="https://sass-lang.com/">Sass.</a></p> 

        <p>It was created by Denise Tench, a student from the September 2019 cohort of the Manchester Codes <a href="https://bit.ly/2VH4I9N">Software Engineering FastTrack course.</a></p>

        <p>The reason I created this app was to lend some support to students using the <a href="https://bit.ly/2YGmMCQ">Pomodoro Technique</a> to help them study.</p>

        <p>This technique advocates short bursts of studying with breaks every 20 to 30 minutes.</p>

        <p>Studying software engineering, especially while juggling a full-time job and personal commitments, is notoriously challenging.</p>

        <p>Encouragement from the developer community, friends, family and peers helps students to keep on going.</p>

        <p>During short breaks, students can come here and click the button for some Chuck Norris-style motivation.</p>

        <p>Remember, you can do it! And if you don't, Chuck will find out and come get you.</p>

        <p>Good luck and keep your goals in sight!</p>

        <footer>
          <div className="footericons">
            <div class="text-center center-block">
                <a href="https://github.com/DeniseTench"><i id="social-gh" className="fa fa-github-square fa-3x social"></i></a>
                <a href="mailto:denise.tench@hotmail.co.uk"><i id="social-em" className="fa fa-envelope-square fa-3x social"></i></a>
	              <a href="https://twitter.com/unlikely_coder"><i id="social-tw" className="fa fa-twitter-square fa-3x social"></i></a>
            </div>
          </div>
        </footer>
</div>
    );
  }
}

export default App;


